import { info } from '@actions/core';
import os from 'os';
import path from 'path';
import { format } from 'util';
export function getBuildSnapshotMeta({ sha, label, }) {
    const restoreKey = `build-size-v1-${label}-`;
    const key = restoreKey + sha;
    const meta = {
        key,
        restoreKey,
        filename: path.join(os.tmpdir(), `${key}.json`),
    };
    info(format('Snapshot meta for the { sha: %s, label: %s }: %O', sha, label, meta));
    return meta;
}
